import React from 'react';
import windmill from '../assets/windmill.svg';
import './ConclusionScreen.scss';
import Button from "react-bootstrap/Button";

function ConclusionScreen({onClick}) {
  return (
    <div className='conclusion-wrapper'>
        <img className='mx-auto d-block' src={windmill} alt="widnmill" />
        <div className='content-wrap'>
            <h2>That Was Easy!</h2>
            <p>Now see how you compare to your fellow citizens and humans on or shared planet.</p>
        </div>
        <div className="content-wrap bottom">
            <h2 className="ready">Ready?</h2>
            <Button variant="success" onClick={() => onClick()}>Go</Button>
        </div>
    </div>
  )
}

export default ConclusionScreen;