import React, { useEffect, useState } from "react";
import "bootstrap/dist/css/bootstrap.css";
import Carousel from "react-bootstrap/Carousel";
import Button from "react-bootstrap/Button";
import Login from "../src/Login/Login";
import WelcomeScreen from "./WelcomeScreen/WelcomeScreen";
import QuestionComponent from "./QuestionComponent/QuestionComponent";
import "../src/CarbonApp.scss";
import slide1 from './assets/slide1.png';
import slide2 from './assets/slide2.png';
import slide3 from './assets/slide3.png';
import ConclusionScreen from "./ConclusionScreen/ConclusionScreen";
import DashboardScreen from "./DashboardScreen/DashboardScreen";

export default function CarbonApp() {
  const [login, setLogin] = useState(false);
  const [showWelcome, setShowWelcome] = useState(false);
  const [showQuestionairre, setQuestionairre] = useState(false);
  const [showConclusion, setShowConclusion] = useState(false);
  const [showDashboard, setShowDashboard] = useState(false);
  const [calculatedCarbonVal, setCalculatedCarbonVal] = useState();

  useEffect(() => {
    setLogin(false);
  }, []);

  const getLoginForm = () => {
    setLogin(true);
  };
  const onShowWelcome = () => {
    setLogin(false);
    setShowWelcome(true);
  }
  const onWelcomeNextClick = () => {
    console.log("WELOME NEXT");
    setLogin(false);
    setShowWelcome(false);
    setQuestionairre(true);
  };

  const onQuestionFinalClick = (param) => {
    setShowConclusion(true);
    setQuestionairre(false);
    setCalculatedCarbonVal(param);
  };

  const onConclusionClick = () => {
    setShowConclusion(false);
    setShowDashboard(true);
  }

  return (
    <div className="carbon-container">
      {login && (
        <Login setShowWelcome={onWelcomeNextClick} />
      )} {!login && !showWelcome && !showQuestionairre && !showConclusion && !showDashboard &&
        <div className="slider-wrapper">
          <Carousel controls={false}>
            {/* <Carousel.Item interval={150000000}> */}
            <Carousel.Item>
              <img className="d-block w-100" src={slide1} alt="slide 1" />
              <Carousel.Caption>
                <h2>Our future to choose</h2>
                <p>Respont with positive, practical actions</p>
              </Carousel.Caption>
            </Carousel.Item>
            <Carousel.Item>
              <img className="d-block w-100" src={slide2} alt="slide 2" />
              <Carousel.Caption>
                <h2>Shift the culture</h2>
                <p>
                  Advocate for change by discovering satisfying ways to live{" "}
                </p>
              </Carousel.Caption>
            </Carousel.Item>
            <Carousel.Item interval={500}>
              {/* <Carousel.Item> */}
              <img className="d-block w-100" src={slide3} alt="slide 3" />
              <Carousel.Caption>
                <h2>Restore a healthy Earth</h2>
                <p>Joining a global community caring for our shared planet</p>
              </Carousel.Caption>
            </Carousel.Item>
          </Carousel>
          <Button
            className="carbon-signin"
            variant="success"
            onClick={getLoginForm}
          >
            Sign In
          </Button>
        </div>
        // <ConclusionScreen />
      }
      {showWelcome && (
        <WelcomeScreen onClick={onWelcomeNextClick} />
      )}
      {showQuestionairre && (
        < QuestionComponent onClick={onQuestionFinalClick} />
      )}
      {showConclusion && (
        <ConclusionScreen onClick={onConclusionClick}/>
      )}
      {showDashboard && (
        <DashboardScreen carbonValues={calculatedCarbonVal} />
      )}
    </div>
  );
}
