import React, { useState, useEffect } from 'react';
import "bootstrap/dist/css/bootstrap.min.css";
import "../QuestionComponent/QuestionComponent.scss";
import Button from "react-bootstrap/Button";
import ButtonGroup from 'react-bootstrap/ButtonGroup';
import ToggleButton from 'react-bootstrap/ToggleButton';

export default function QuestionComponent({onClick}) {
    const [lastQuestion, setLastQuestion] = useState(false);
    const [questionairre, setquestionairre] = useState(0);
    const [radioValue, setRadioValue] = useState(null);
    const [nextDisable, setNextDisable] = useState(true);
    const [carbonFootprint, setCarbonFootprint] = useState( {
        drive: 0,
        flights: 0,
        nonVeg: 0,
        cleanPower: 0,
        purchaseThings: 0
    });
    const questionArray = [
        {
            title: "How many kilometers did you drive in the last year?",
            options: [
                {label: "Zero", value: 0}, {label: "Less than 7500", value: 7499}, {label: "Between 7500 - 15000", value: 14999}, {label: "More than 15000", value: 19999}
            ],
            coefficent: 0.1221 // 122.1gram in 1km
        }, {
            title: "How many roundtrip flights have you taken in the last year?",
            options: [
                {label: "Zero", value: 0}, {label: "One to Two", value: 2}, {label: "Three to Five", value: 5}, {label: "More than Five", value: 9}
            ],
            coefficent: 0.255 // 255gram in 1km
        }, {
            title: "How many days per week do you eat meat or seafood?",
            options: [
                {label: "Zero", value: 0}, {label: "One to Three", value: 3}, {label: "Four to Six", value: 6}, {label: "Every Day", value: 7}
            ],
            coefficent: 0.100 
        }, {
            title: "How is your electricity powered? (eg. of clean energy are solar and wind)",
            options: [
                {label: "100% Clean", value: 1}, {label: "50% Clean", value: 0.5}, {label: "Not Clean", value: 0}, {label: "Use local average", value: 0.3}
            ],
            coefficent: 0.01 // 100kg traditional => 1kg clean
        }, {
            title: "Spending on new stuff per month (clothes, furniture and other goods)?",
            options: [
                {label: "Less than 10,000", value: 10000}, {label: "Between 10,000 to 50,000", value: 49999}, {label: "Between 50,000 to 1,00,000", value: 99999}, {label: "More than 1,00,000", value: 150000}
            ],
            coefficent: 0.01 //
        }
    ];

    const calculateFun = (value, idx) => {
        switch(idx) {
            case 0: console.log('drive', idx, value);
                    setCarbonFootprint({...carbonFootprint, drive: value * questionArray[idx].coefficent})
            break;
            case 1: console.log('flights', idx, value);
            setCarbonFootprint({...carbonFootprint, flights: 2000 * value * questionArray[idx].coefficent})
            break;
            case 2: console.log('nonVeg', idx, value);
            setCarbonFootprint({...carbonFootprint, nonVeg: (value * questionArray[idx].coefficent) * 365})
            break;
            case 3: console.log('cleanPower', idx, value);
            setCarbonFootprint({...carbonFootprint, cleanPower: ((8000 - 8000 * value) * questionArray[idx].coefficent) * 12})
            break;
            case 4: console.log('purchaseThings', idx, value);
            setCarbonFootprint({...carbonFootprint, purchaseThings: (value * questionArray[idx].coefficent)})
            break;
            default: break;
        }
    };

    const handleSubmit = (e) => {
        calculateFun(radioValue, questionairre);
        if (questionairre < questionArray.length - 1) {
            setquestionairre(prevValue => {
                return prevValue + 1;
            })
            setRadioValue(null);
            setNextDisable(true);
        } else{
            console.log('last');
            console.log(carbonFootprint)
            setLastQuestion(true);
        }
    };

    useEffect(() => {
        console.log(carbonFootprint);
        if(lastQuestion) {
            let finalResult = 0;
            for (let item in carbonFootprint) {
                finalResult+=carbonFootprint[item];
            }
            console.log(finalResult);
            onClick({...carbonFootprint, totalCarbonEmission: finalResult});
        }
    }, [carbonFootprint]);

    const handleChange = (idx, value) => {
        setNextDisable(false);
        setRadioValue(value);
        console.log(idx);
    };


    return (
        <div className="question-screen">
            <div className="content-wrap top">
                <div className='title-wrap'>
                <h3 className="header">{questionArray[questionairre].title}</h3>
                <p className="description">Answer as best you can</p>
                <p className="description">You can update or add more details after initial setup.</p>
                </div>
                <ButtonGroup>
                    {questionArray[questionairre].options.map((radio, idx) => (
                        <ToggleButton
                            key={idx}
                            id={`radio-${idx}`}
                            type="radio"
                            // variant=""
                            name="radio"
                            value={radio.value}
                            checked={radioValue === radio.value}
                            onChange={(e) => handleChange(idx, radio.value)}
                        >
                            {radio.label}
                        </ToggleButton>
                    ))}
                </ButtonGroup>
            </div>
            <div className="content-wrap bottom">
                <Button variant="success" disabled={nextDisable} onClick={handleSubmit}>Next</Button>
            </div>
        </div>
    )
}
